<template>
	<view class="scan-container">
		<view class="sacn-input-box">
			<u--input
				placeholder="请输入设备IMEI码"
				shape="circle"
				suffixIcon="search"
	    		suffixIconStyle="color: #909399"
				v-model="scanValue"
				@change="handleScanChange"
			></u--input>
			<view>
				<i class="iconfont" @click="handleScan">&#xe713;</i>
			</view>
		</view>

		<view v-if="scanResult !== null" class="scan-result">
			<view class="scan-content">
				<text class="scan-content-title">名称不超过8个字</text>
				<view class="scan-content-item">
					<view class="scan-content-item-left">
						<view class="scan-content-item-left-item">
							<i class="iconfont" style="padding-right: 8px">&#xe70b;</i>
							<i class="iconfont" style="padding-right: 4px">&#xe70d;</i>
							<text style="padding-right: 10px">{{batteryNum}}%</text>
							<i class="iconfont" style="padding-right: 10px">&#xe70c;</i>
							<text>{{code}}</text>
						</view>
						<view class="scan-content-item-left-item">
							<i class="iconfont" style="padding-right: 8px">&#xe70e;</i>
							<view class="remark-content">{{ remark }}</view>
						</view>
						<view class="scan-content-item-left-item">
							<i class="iconfont" style="padding-right: 4px">&#xe70f;</i>
							<text>{{ location }}</text>
						</view>
					</view>
					<view class="scan-content-item-right">
						<i class="iconfont" >&#xe711;</i>
					</view>
				</view>
				<u-button type="primary" :plain="true" text="编辑设备信息" @click="handleEdit"></u-button>
			</view>
			<u-line color="#efefef"></u-line>
			<view class="scan-content">
				<view class="scan-content-item">
					<view class="scan-content-item-bottom">
						<view class="scan-content-item-bottom-item">
							<view>
								<text>当前流量：</text>
								<text>{{ valveInfo.currentFlow }}m³/h</text>
							</view>
							<view>
								<text>本次时长：</text>
								<text>{{ valveInfo.currentDuration  }}h</text>
							</view>
						
						</view>
						<view class="scan-content-item-bottom-item">
							<view style="margin-left: 20px">
								<text>剩余时长：</text>
								<text>{{ valveInfo.remainDuration }}h</text>
							</view>
							<view style="margin-left: 20px">
								<text>本次累计：</text>
								<text>{{ valveInfo.allDuration  }}m³
								</text>
							</view>
						</view>
					</view>
					<view class="scan-content-item-right">
						<i class="iconfont" >&#xe711;</i>
					</view>
				</view>
				<u-button type="primary" :plain="true" text="控制阀门" @click="handleControlValve"></u-button>
			</view>
		</view>

        <view v-else class="scan-no-result">
            <i class="iconfont">&#xe71a;</i>
            <view class="scan-result-message">非常抱歉！该设备还未绑定。</view>
            <view class="scan-result-tip">请前往【设备管理中扫码添加设备】</view>
        </view>
		<view class="scan-guide">
			<view class="scan-guide-item">
				<text class="scan-guide-item-text">操作步骤如下</text>
			</view>
			<view class="scan-guide-item">
				<image class="scan-guide-item-image first" src="/static/images/scan-step_01.jpg"></image>
				<text class="scan-guide-item-text">第一步</text>
				<text class="scan-guide-item-text">点击【设备管理】，进入到设备管理页面</text>
			</view>
			<view class="scan-guide-item">
				<image class="scan-guide-item-image second" src="/static/images/scan-step_02.jpg"></image>
				<text class="scan-guide-item-text">第二步</text>
				<text class="scan-guide-item-text">点击【扫码输入框】，然后扫描设备上的条形码</text>
			</view>
			<view class="scan-guide-item">
				<image class="scan-guide-item-image third" src="/static/images/scan-step_03.jpg"></image>
				<text class="scan-guide-item-text">第三步</text>
				<text class="scan-guide-item-text">在弹出的窗口内，填写设备信息并点击确定即可完成添加</text>
				<text class="scan-guide-item-text">备注：设备所属地块，为设备安装地址信息，</text>
				<text class="scan-guide-item-text">方便后续根据地块分组管理设备。</text>
				<text class="scan-guide-item-text">设备名称：为设备命名，方便后续查找</text>
			</view>
		</view>
	</view>
</template>
<script>
	import { ScanCode } from '@/utils/index'
	export default {
		data() {
			return {
				scanResult: null,
				scanValue: '',
				batteryNum: '98',
				code: 864452060202551,
				remark: '18365978466，隔壁老樊，18657908864',
				location: '西地块',
				valveInfo: {
					currentFlow: '3',
					remainDuration: '0.5',
					currentDuration: '3',
					allDuration: '50'
				}
			}
		},
		onLoad() {
			const eventChannel = this.getOpenerEventChannel();
			if(eventChannel) {
				eventChannel.on('scanEvent', function(data) {
					this.scanResult = data
					console.log(this.scanResult);
					
				})
			}

		},
		methods: {
			// 编辑设备信息
			handleEdit() {

			},

			// 控制阀门
			handleControlValve() {},

			handleScanChange(val) {
				this.scanValue = val
				console.log('val', val);
			},

			async handleScan() {
				try {
					const scanResult = await ScanCode()
					console.log('scanResult', scanResult);
				}catch(error) {
					console.log('error', error);
					
				}
			}
		}
	}
</script>

<style lang="scss" scoped>
	.scan-container {
		.sacn-input-box {
			display: flex;
			align-items: center;
			padding: 12px;
			background: $u-main-color;
			::v-deep .u-input {
				flex: 1;
				background: $u-dark-color;
				border: none;
				&__content__field-wrapper__field  {
					color: #fff !important;
				}
			}
			i {
				color: #fff;
				padding-left: 26px;
				padding-right: 18px;
			}
		}
		.scan-result {
			.scan-content {
				padding: 20px 25px;
				&-title {
					display: block;
					color: #4F4F4F;
					margin-bottom: 10px;
				}
				&-item {
					display: flex;
					align-items: center;
					margin-bottom: 24px;
					&-left {
						flex: 1;
						&-item {
							display: flex;
							align-items: center;
							font-size: 14px;
							color: #bebebe;
							margin-top: 4px;
							.remark-content {
								position: relative;
								display: blcok;
								flex: 1;
								overflow: hidden;
								white-space: nowrap; 
								text-overflow: ellipsis;
							}
						}
					}
					&-right {
						width: 20px;
						color: #bebebe;
					}
					&-bottom {
						display: flex;
						flex: 1;
						&-item {
							font-size: 14px;
							color: #bebebe;
						}
					}
					
				}
			}
		}
		.scan-no-result {
			margin-top: 40px;
			margin-bottom: 50px;
			text-align: center;
			.iconfont {
				display: block;
				font-size: 40px;
				color: #E99D42;
				margin-bottom: 30px;
			}
			.scan-result-message {
				font-size: 18px;
				color: #101010;
				margin-bottom: 19px;
			}
			.scan-result-tip {
				font-size: 18px;
				color: #E99D42;
			}
		}
		.scan-guide {
			padding-bottom: 60px;
			&-item {
				display: flex;
				flex-direction: column;
				align-items: center;
				&-image {
					display: block;
					margin: 20px 0;
					// uniapp 图片默认高度225，这里需要修改一下
					&.first {
						height: 560px;
					}
					&.second {
						height: 450px;
					}
					&.third {
						height: 580px;
					}
				}
				&-text {
					display: block;
					font-size: 14px;
					color: #000;
					&+ .scan-guide-item-text {
						padding-top: 6px;
					}
				}

			}
		}
	}
</style>